const { Peer } = require('./peer');
const { Identity } = require('./identity');
const { EventEmitter } = require('events');
const assert = require('assert');
const Memory = require('twlv-storage-memory');
const getIps = require('./lib/get-ips');
const assertion = require('./lib/assertion');
const facade = require('./lib/facade');
const { Logger } = require('twlv-logger');
const { Codec } = require('./codec');
const pkg = require('./package.json');
const { KIND_APP, KIND_CHANNEL, KIND_DISCOVERY } = require('./module');
const { hidden, readonly } = require('./lib/prop');
const { Handshake, Relay } = require('./controller');

const LOOKUP_TIMEOUT = 10000;

/**
 * Object to initiate swarm network or join to network
 */
class Swarm extends EventEmitter {
  /**
   * getter for global Swarm framework id
   */
  static get FRAMEWORK_ID () {
    return 'twlv';
  }

  /**
   *
   * @param {object} options Options to initiate swarm
   *
   * Available options:
   *
   * networkId - The network id
   * storage - Storage instance
   */
  constructor ({ networkId = '0', storage = new Memory() } = {}) {
    super();

    assert(networkId, 'Swarm must define networkId');
    assert(storage, 'Swarm must define storage');

    this.peers = [];
    this.sessions = [];
    this.apps = [];
    this.discoveries = [];
    this.channels = [];
    this.started = false;

    readonly(this, 'version', pkg.version);
    readonly(this, 'networkId', networkId);
    hidden(this, 'bootPeers', []);
    hidden(this, 'storage', storage);
    hidden(this, 'logger');

    readonly(this, 'handshakeController', new Handshake());
    readonly(this, 'relayController', new Relay());

    hidden(this, '_lookupUnits', []);
  }

  get discovery () {
    console.warn('Swarm#discovery is deprecated, please use Swarm#discoveries');
    return this.discoveries;
  }

  get address () {
    return this.identity.address;
  }

  get publicKey () {
    return this.identity.publicKey;
  }

  // TODO: revisit generate urls to include current connected socket urls
  get urls () {
    let ips = getIps();
    let urls = [];
    this.channels.forEach(channel => {
      if (typeof channel.getUrls === 'function') {
        let result = channel.getUrls();
        if (result) {
          urls = urls.concat(result);
        }
      }

      // DEPRECATED: channel.formatUrl()
      if (typeof channel.formatUrl === 'function') {
        console.warn(`Warning: Deprecated channel#formatUrl(), ${channel}`);

        ips.forEach(ip => {
          let chanUrl = channel.formatUrl(ip);
          if (chanUrl && urls.indexOf(chanUrl) === -1) {
            urls.push(chanUrl);
          }
        });
      }
    });
    return urls;
  }

  get advertisement () {
    return {
      address: this.address,
      publicKey: this.publicKey,
      urls: this.urls,
      apps: this.apps.map(app => app.name),
      channels: this.channels.map(channel => channel.name),
      discoveries: this.discoveries.map(discovery => discovery.name),
    };
  }

  addBootPeer (peer) {
    assert(typeof peer === 'string' || peer instanceof Peer, 'Boot peer must be string or instanceof peer');

    this.bootPeers.push(peer);
  }

  async addChannel (channel) {
    assertion.channel(channel);

    this.channels.push(channel);

    if (this.started) {
      await channel.start(facade(this, { module: channel }));
    }
  }

  async removeChannel (channel) {
    let index = this.channels.indexOf(channel);
    if (index !== -1) {
      this.channels.splice(index, 1);

      await channel.stop();
    }
  }

  getChannel (name) {
    return this.channels.find(channel => channel.name === name);
  }

  getChannelByUrl (url) {
    let proto = url.split(':').shift();
    return this.channels.find(channel => channel.proto === proto);
  }

  async send ({ address, kind, module, command, payload }) {
    assert(typeof address === 'string', 'Address must be string');
    assertion.kind(kind);

    if (
      (kind === KIND_APP && !this.getApp(module)) ||
      (kind === KIND_CHANNEL && !this.getChannel(module)) ||
      (kind === KIND_DISCOVERY && !this.getDiscovery(module))
    ) {
      throw new Error(`Cannot send to unregistered module`);
    }

    let peer;
    let session = await this.get(address);
    if (!session) {
      peer = await this.lookup(address);
      assert(peer, `Peer not found, address=${address}`);

      try {
        session = await this.connect(peer);
      } catch (err) {
        console.warn('Warning: Failed to establish direct connection');
      }
    }

    if (
      (kind === 'app' && session.advertise.apps.indexOf(module) === -1) ||
      (kind === 'channel' && session.advertise.channels.indexOf(module) === -1) ||
      (kind === 'discovery' && session.advertise.discoveries.indexOf(module) === -1)
    ) {
      throw new Error('Cannot send message of unsupported module');
    }

    assert(session, `Session not found, address=${address}`);

    return session.write({ kind, module, command, payload });
  }

  broadcast ({ kind, module, command, payload }) {
    assertion.kind(kind);
    if (
      (kind === KIND_APP && !this.getApp(module)) ||
      (kind === KIND_CHANNEL && !this.getChannel(module)) ||
      (kind === KIND_DISCOVERY && !this.getDiscovery(module))
    ) {
      throw new Error(`Cannot send to unregistered module`);
    }

    this.sessions.forEach(session => {
      if (!session.handshaken) {
        return;
      }

      if (
        (kind === 'app' && session.advertise.apps.indexOf(module) === -1) ||
        (kind === 'channel' && session.advertise.channels.indexOf(module) === -1) ||
        (kind === 'discovery' && session.advertise.discoveries.indexOf(module) === -1)
      ) {
        return;
      }

      session.write({ kind, module, command, payload });
    });
  }

  relay ({ address, ttl, kind, module, command, payload }, identity) {
    assertion.kind(kind);
    this.relayController.relay({ address, ttl, kind, module, command, payload }, identity);
  }

  get (address) {
    assert(typeof address === 'string', 'Address must be string');

    return this.sessions.find(session => session.address === address);
  }

  /**
   * Lookup peer by its address
   *
   * The function will try to find from connected peers first.
   *
   * When peer with specified address not found, then lookup to discovery methods.
   *
   * @param {string} address
   */
  async lookup (address) {
    this.logger.log(`Looking up address=${address}...`);

    // lookup from available local peers
    let peer = this.peers.find(peer => peer.address === address);
    if (peer) {
      return peer;
    }

    // FIXME: reimplement using lookup unit to avoid double lookup for the same
    // address or timeout leaking
    // when not found, do not lookup remotely when there is no discovery method
    if (this.discoveries.length) {
      // when not found, try to lookup remotely by discovery
      let definition = await new Promise(resolve => {
        let timeout = setTimeout(resolve, LOOKUP_TIMEOUT);
        this._lookupUnits.push({ timeout, resolve });
        this.discoveries.forEach(async discovery => {
          try {
            let definition = await discovery.discover(address);
            if (definition) {
              let index = this._lookupUnits.indexOf(timeout);
              if (index !== -1) {
                this._lookupUnits.splice(index, 1);
              }
              clearTimeout(timeout);
              this.logger.log(`Discovery ${discovery} found ${definition}`);
              return resolve(definition);
            }
          } catch (err) {
            console.error('Discovery lookup caught error,', err);
          }
        });
      });

      if (definition) {
        peer = this._putPeer(definition);
      }
    }

    if (!peer) {
      this.logger.log(`Failed attempt to lookup address=${address}`);
    }

    return peer;
  }

  _putPeer (newPeer) {
    assert(newPeer instanceof Peer, 'Peer to put must be instance of peer');

    let peer = this.peers.find(peer => peer.address === newPeer.address);

    if (peer) {
      peer.update(newPeer);
    } else {
      this.peers.push(newPeer);
      peer = newPeer;
    }

    return peer;
  }

  async connect (definition, forceDirect = false) {
    assert(this.started, 'Cant connect on stopped swarm');
    assert(definition, 'Connect must define definition');

    this.logger.log('Connecting to %o', definition);

    if (typeof definition === 'string') {
      let url = definition;
      let channel = this.getChannelByUrl(url);
      assert(channel, `Dial failed, suitable channel not found for ${url}`);
      let socket = await channel.connect(url);
      let session = await this._addSessionWithSocket(socket, true);
      return session;
    } else if (definition instanceof Peer) {
      let { urls } = definition;
      for (let url of urls) {
        let channel = this.getChannelByUrl(url);
        if (!channel) continue;

        try {
          let session = await this.connect(url);
          return session;
        } catch (err) {
          console.error(`Error on connect to ${url}`, err);
        }
      }

      if (!forceDirect) {
        this.logger.log('Failed establish direct connection, try relay');
        try {
          let session = await this.relayConnect(definition);
          return session;
        } catch (err) {
          console.error('Failed to establish relay connection', err);
        }
      }

      throw new Error(`Can't connect to ${definition}`);
    }

    throw new Error(`Failed to connect to unknown definition ${JSON.stringify(definition)}`);
  }

  async relayConnect (peer, initiate) {
    let session = await this._addSessionWithRelay(peer, true);
    return session;
  }

  async disconnect (session) {
    session.removeAllListeners();
    await session.end();
    this.emit('disconnect', session);

    let index = this.sessions.indexOf(session);
    if (index !== -1) {
      this.sessions.splice(index, 1);
    }
  }

  async start () {
    this.started = true;
    await this._bootIdentity();
    await this._bootChannels();
    await this._bootApps();
    await this._bootDiscoveries();
    await this._bootControllers();
    await this._bootSessions();

    this.logger.log('Started');

    this.emit('start');
    // await sleep();
  }

  async stop () {
    await this._debootSessions();
    await this._debootControllers();
    await this._debootDiscoveries();
    await this._debootApps();
    await this._debootChannels();

    this.started = false;
    this.logger.log('Stopped');

    await this._debootIdentity();

    this.emit('stop');
    // await sleep();
  }

  async _bootControllers () {
    await this.handshakeController.up(this);
    await this.relayController.up(this);
  }

  async _debootControllers () {
    await this.handshakeController.down();
    await this.relayController.down();
  }

  async _bootSessions () {
    await Promise.all(this.bootPeers.map(async url => {
      try { await this.connect(url); } catch (err) {}
    }));
  }

  async _debootSessions () {
    await Promise.all(this.sessions.map(session => this.disconnect(session)));
    this.sessions = [];

    // TODO: workaround lookup units
    await this._lookupUnits.forEach(({ timeout, resolve }) => {
      clearTimeout(timeout);
      resolve();
    });
    this._lookupUnits = [];
  }

  async _bootApps () {
    await Promise.all(this.apps.map(async app => {
      try {
        await app.start(facade(this, { module: app }));
      } catch (err) {
        console.error(`Failed to start app ${app.name}, cause: ${err.message}`);
      }
    }));
  }

  async _debootApps () {
    await Promise.all(this.apps.map(async app => {
      try {
        await app.stop();
      } catch (err) {
        console.error(`Failed to stop app ${app.name}`, err);
      }
    }));
  }

  async _onMessage ({ address, kind, module, command, mode, payload }) {
    // TODO: implement rate limit

    // this.logger.log(`Inbound %o`, { address, kind, module, command, mode, payload });

    this.emit('message', { address, kind, module, command, payload });

    try {
      if (kind === '!') {
        if (module === 'relay') {
          await this.relayController._onMessage({ address, kind, module, command, mode, payload });
          return;
        } else if (module === 'handshake') {
          await this.handshakeController._onMessage({ address, kind, module, command, mode, payload });
          return;
        } else if (module === 'mock') {
          // for testing purpose only
          return;
        }

        throw new Error('Unimplemented handler for control message');
      }

      let theModule;
      switch (kind) {
        case KIND_APP:
          theModule = this.getApp(module);
          break;
        case KIND_CHANNEL:
          theModule = this.getChannel(module);
          break;
        case KIND_DISCOVERY:
          theModule = this.getDiscovery(module);
          break;
      }

      if (theModule) {
        await theModule.onMessage({ address, command, payload });
      }
    } catch (err) {
      console.error('Error on message routing', err);
    }
  }

  getApp (name) {
    return this.apps.find(app => app.name === name);
  }

  async addApp (app) {
    assertion.app(app);

    this.apps.push(app);

    if (this.started) {
      await app.start(facade(this, { module: app }));
    }
  }

  async removeApp (app) {
    let index = this.apps.indexOf(app);
    if (index !== -1) {
      this.apps.splice(index, 1);

      await app.stop();
    }
  }

  async _bootChannels () {
    await Promise.all(this.channels.map(async channel => {
      try {
        await channel.start(facade(this, { module: channel }));
      } catch (err) {
        console.error(`Failed to start channel ${channel.proto}, cause: ${err.message}`);
      }
    }));
  }

  async _debootChannels () {
    await Promise.all(this.channels.map(async channel => {
      try {
        await channel.stop();
      } catch (err) {
        console.error(`Failed to stop channel ${channel.proto}, cause: ${err.message}`);
      }
    }));
  }

  async incoming (socket) {
    if (!socket.pipe) {
      console.warn('Warning: Deprecated incoming({socket}) please use incoming(socket)');
      socket = socket.socket;
    }

    try {
      await this._addSessionWithSocket(socket);
    } catch (err) {
      // TODO: notify socket about error, must define error messaging
      console.error('Error on incoming', err);
    }
  }

  async _addSessionWithSocket (socket, initiate = false) {
    let session = await this.handshakeController.handshakeWithSocket(socket, initiate);
    this._addSession(session);
    return session;
  }

  async _addSessionWithRelay (peer, initiate = false) {
    let session = await this.handshakeController.handshakeWithRelay(peer, initiate);
    this._addSession(session);
    return session;
  }

  _addSession (session) {
    session.on('data', this._onMessage.bind(this));
    session.on('end', () => this.disconnect(session));

    this._putPeer(session.peer);
    this.emit('connect', session);
    this.sessions.push(session);
  }

  async initIdentity () {
    this.identity = new Identity();
    await this.storage.write('privkey.pem', this.identity.privateKey);
  }

  async _bootIdentity () {
    if (await this.storage.exists('privkey.pem')) {
      this.identity = new Identity(await this.storage.read('privkey.pem'));
    } else {
      await this.initIdentity();
    }

    this.logger = new Logger('twlv-swarm', this.identity.address);
    this.codec = new Codec(this.identity);
  }

  _debootIdentity () {
    this.identity = null;
    this.codec = null;
  }

  async _bootDiscoveries () {
    await Promise.all(this.discoveries.map(method => method.start(facade(this, { module: method }))));
  }

  async _debootDiscoveries () {
    await Promise.all(this.discoveries.map(method => method.stop()));
  }

  getDiscovery (name) {
    return this.discoveries.find(method => method.name === name);
  }

  async addDiscovery (discovery) {
    assertion.discovery(discovery);

    this.discoveries.push(discovery);

    if (this.started) {
      await discovery.start(facade(this, { module: discovery }));
    }
  }

  async removeDiscovery (method) {
    let index = this.discoveries.indexOf(method);
    if (index !== -1) {
      this.discoveries.splice(index, 1);

      await method.stop();
    }
  }
}

module.exports = { Swarm };



