const { Channel } = require('twlv-swarm');
const { Socket } = require('./socket');
const http = require('http');
const ioserver = require('socket.io');
const ioclient = require('socket.io-client');
const { parse } = require('url');
const assert = require('assert');
const getIps = require('twlv-swarm/lib/get-ips');

class SocketIo extends Channel {
  constructor ({ port, server, listening = true }) {
    super('socketio');

    if (listening) {
      assert(port || server, 'Socketio Channel must define port or server instance');
    }

    this.listening = listening;
    this.port = port;
    this.server = server;
    this.localServer = Boolean(!server);
  }

  async up () {
    if (!this.listening) {
      return;
    }

    if (this.localServer) {
      this.server = http.Server((req, res) => {
        res.end(JSON.stringify({ name: 'twlv-channel-socketio' }));
      });
    }

    let io = ioserver(this.server);
    io.on('connection', ioSocket => {
      let socket = this.ioSocket2Socket(ioSocket);
      this.incoming(socket);
    });

    if (this.localServer) {
      await new Promise(resolve => {
        this.server.listen(this.port, resolve);
      });
    }
  }

  down () {
    if (!this.listening) {
      return;
    }

    if (this.localServer) {
      this.server.close();
    }
  }

  onMessage () {
    // noop
  }

  async connect (url) {
    let ioSocket = ioclient(this.p2p2HttpUrl(url));

    await new Promise((resolve, reject) => {
      ioSocket.on('connect', resolve);
    });

    let socket = this.ioSocket2Socket(ioSocket);
    return socket;
  }

  p2p2HttpUrl (url) {
    let parsed = parse(url);
    return `http://${parsed.host}`;
  }

  ioSocket2Socket (ioSocket) {
    return new Socket(ioSocket);
  }

  getUrls () {
    if (!this.listening) {
      return [];
    }

    let ips = getIps();
    return ips.map(ip => `${this.proto}://${ip}:${this.port}`);
  }

  dump () {
    let { proto, port } = this;
    return { proto, port };
  }
}

module.exports = SocketIo;



